@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap');


.column {
	width: 100%;
}

@media (min-width: 600px) {
	.column {
		width: 50%;
	}
}

body {
	/*background: #FBF8BE;*/
	background: white;
	font-family: 'Share Tech Mono', monospace;

}


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

li,
a,
button {
	/* font-family: "Montserrat", sans-serif; */
	font-family: 'Share Tech Mono', monospace;

	font-weight: 500;
	font-size: 16px;
	color: black;
	text-decoration: none;
}


header {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	/*background-color: #234E70;*/
	padding: 0 25px;
	transition: 0.3s;
	color: black;

}

.logo {
	font-family: "Montserrat", sans-serif;
	cursor: pointer;
	font-weight: bolder;
	font-size: 20px;
	margin-right: auto;
	color: black;
}


.nav__links {
	display: flex;
	justify-content: center;
	flex-direction: row;
	align-items: center;
	z-index: 1;
	transition: 0.5s;


}

.nav__links li {
	list-style-type: none;

}

.nav__links li a {
	color: black;
	text-decoration: none;
	display: block;
	padding: 40px 25px;
	line-height: 1;
	transition: 0.3s;
}

.nav__links li a:hover {
	box-shadow: 0 -5px 0px #fff inset,
		500px 0 0 rgba(255, 255, 255, 0.03) inset;
	padding: 35px 25px 45px 25px;
}

button {
	margin-left: 20px;
	padding: 9px 25px;
	background-color: #Edf0f1;
	color: #24252A;
	border: none;
	border-radius: 1px;
	cursor: pointer;
	transition: all 0.3s ease 0s
}

â€‹ button:hover {
	/*background-color: #FBF8BE;
	color: black;*/

	background-color: black;
	color: white;
}

.centreContainer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	min-height: 60vh;
}